<template>
  <div class="login">
    <mainLeft class="animated fadeInLeft delay_0"></mainLeft>
    <div class="login-right animated fadeInRight delay_0">
      <div class="login-top">
        <h2>登录</h2>
        <p>娜乌西卡科技 | 通行证</p>
      </div>
      <el-form ref="form" :model="form" label-width="80px">
        <el-input v-model="form.name" placeholder="账号"></el-input>
        <el-input v-model="form.name" placeholder="密码"></el-input>
        <el-button type="primary" @click="onSubmit">立即创建</el-button>
      </el-form>
      <div class="login-in">
        <span>立即注册</span>
        <span>忘记密码？</span>
      </div>
    </div>
  </div>
</template>

<script>
import mainLeft from "~/components/mainLeft.vue";

export default {
  data() {
    return {
      form: {
        name: ""
      }
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  },
  components: {
    mainLeft
  }
};
</script>

<style scoped lang="scss">
.login {
  width: 1200px;
  margin: auto;
  margin-bottom: 30px;
  padding: 50px;
  .login-right {
    overflow: hidden;
    background: #fff;
    padding: 150px;
    .login-top {
      text-align: center;
      position: relative;
      h2 {
        margin-bottom: 15px;
      }
      p{
        margin-bottom: 30px;
      }
    }
    button {
      height: 46px;
      width: 100%;
      background: #242299;
      font-size: 16px;
      border: 0;
      color: #fff;
      display: inline-block;
      cursor: pointer;
      box-shadow: 0 0 10px 1px #b5ceff;
      border-radius: 2px;
    }
    .login-in {
      margin: 10px 0;
      span:last-child {
        float: right;
      }
    }
  }
}

.el-input {
  margin-bottom: 15px;
}

.login::after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
}
</style>



